import React from 'react'
import './SchoolProfile.css'
import Tabs from '../../Components/tabs'
import Paper from '@material-ui/core/Paper';
import fire from '../../config/firebase';

 class SProfile extends React.Component {
constructor(props){

    super(props)
    this.state={
       contact:'',
        img:'https://scontent.fkhi1-1.fna.fbcdn.net/v/t1.0-9/39141029_1815432135177542_3235499112908455936_o.jpg?_nc_cat=102&_nc_eui2=AeEo3oP8qDAcOShRuzmkPuv85wb55bw88m9kXAF-GCtrul52pmVRj11fLYTR-U4YP9YgHOkVCJE75_b3hwUkq_ix0_wUOGmop7VIE1Htk1eAjg&_nc_ht=scontent.fkhi1-1.fna&oh=2a4e77a35b422e5e8bd504af33ba762a&oe=5CA29FDE'
   , sName:'',
   facilities:'',
    
      }
}


componentDidMount(){


  fire.database().ref(`schoolList/${this.props.match.params.id}`).once('value',(data)=>{
     
this.setState({img:data.val().img,contact:data.val().contact,sName:data.val().sName,facilities:data.val().facilities})

  })
}


  render() {
    return (
      <div >

<Paper>
        <div className="Pic" style={{background:`url(${this.state.img})`}} ></div>
</Paper>
<Tabs schoolid={this.props.match.params.id}/>


      </div>
    )
  }
}

export default SProfile;