import React, { Component } from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Home as HomeIcon, PowerSettingsNew as LogoutIcon, ExitToApp as LoginIcon } from '@material-ui/icons';
import fire from '../../config/firebase';
import swal from 'sweetalert';
import { connect } from 'react-redux';
import {updateUser, removeUser} from '../../Redux/action/autjAction'

class Logoutbtn extends Component {

constructor(props){
    super(props)
}

    logout(){
        fire.auth().signOut()
        .then(()=>{
          this.props.removeUser()
        })
      }
    render() {

        console.log(this.props)
        return (
            <div>
 <ListItem onClick={() => {  this.logout() }} button >
                <ListItemIcon> <LoginIcon /></ListItemIcon>
                <ListItemText primary={"Logout"} />
              </ListItem>          
              
                </div>
        );
    }
}

const mapStateToProps = (state) => {
    console.log('State',state)
    return {
        user: state.authReducers.user
    }
} 

const mapDispatchToProps = (dispatch) => {
    return {
        removeUser: (user) => dispatch(removeUser(user)),
        // removeUser: (user) => dispatch(removeUser(user))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Logoutbtn)

