import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';

import CardActionArea from '@material-ui/core/CardActionArea';

import MoreVertIcon from '@material-ui/icons/MoreVert';
import './Card.css'

const styles = theme => ({
  card: {
    maxWidth: 400,
    display:'block'
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  actions: {
    display: 'flex',
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
    marginLeft: 'auto',
    [theme.breakpoints.up('sm')]: {
      marginRight: -8,
    },
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
});

class SchoolCard extends React.Component {
  state = { expanded: false };

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };

  render() {
    const { classes,Slink,contact,email,facilities,fees,sName } = this.props;

    return (
<div style={{display:'inline-block',margin:'10px',minWidth:'260px'}}>

    
      <Card className={classes.card}>

         <CardActionArea>


        <CardMedia
          className={classes.media}
          image={this.props.pic}
          title="Paella dish"
        />
<div         className="SchoolNameHeader"  style={{textAlign:'left',marginTop: "-80px !important", textShadow: "0px 0px 5px black !important"}}>
             <CardHeader
          action={
            <IconButton>
              <MoreVertIcon />
            </IconButton>
          }

          avatar={
              <img style={{maxWidth:50,maxHeight:50}} src="https://asfpsc.edu.pk/wp-content/uploads/2017/08/School-New-Monogram.png" />
          }
          act
          title={<b style={{color:'white'}}>{sName}</b>}
          subheader={<span style={{color:'white',fontSize:13}}>Updated: 26/11/18</span>}
        />
        </div>
        <CardContent>
          <Typography component="p">
            Our School Has Good Play Ground, Better enviroment for students, We also has a canteen.
          </Typography>
        </CardContent>
     
        </CardActionArea>
      </Card>
      </div>
    );
  }
}

SchoolCard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SchoolCard);