import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PhoneIcon from '@material-ui/icons/Phone';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import {Info as InfoIcon,Videocam as VideoIcon, Star as ReviewIcon,Warning as ComplaintIcon} from '@material-ui/icons';
import Review from '../Reviews'
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import fire from '../../config/firebase';

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

const styles = theme => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
});

class ScrollableTabsButtonForce extends React.Component {

  
  constructor(props){

    super(props)

    this.state={
      contact:'',
       img:'https://scontent.fkhi1-1.fna.fbcdn.net/v/t1.0-9/39141029_1815432135177542_3235499112908455936_o.jpg?_nc_cat=102&_nc_eui2=AeEo3oP8qDAcOShRuzmkPuv85wb55bw88m9kXAF-GCtrul52pmVRj11fLYTR-U4YP9YgHOkVCJE75_b3hwUkq_ix0_wUOGmop7VIE1Htk1eAjg&_nc_ht=scontent.fkhi1-1.fna&oh=2a4e77a35b422e5e8bd504af33ba762a&oe=5CA29FDE'
  , sName:'',
  facilities:'',
  value:0
   
     }
  }


  handleChange = (event, value) => {
    this.setState({ value });
  };


  
componentDidMount(){


  fire.database().ref(`schoolList/${this.props.schoolid}`).once('value',(data)=>{
     
this.setState({img:data.val().img,contact:data.val().contact,sName:data.val().sName,facilities:data.val().facilities})

  })
}


  render() {
    const { classes } = this.props;
    const { value } = this.state;

    return (

      
      <div >
        <AppBar position="static" color="default">
          <Tabs
            value={value}
            onChange={this.handleChange}
            scrollable
            scrollButtons="on"
style={{backgroundColor:'#3f51b5'}}
            indicatorColor="secondary"
            textColor="secondary"
          >
            <Tab className="Tabs" label="About" icon={<InfoIcon />} />
            <Tab  className="Tabs" label="Reviews" icon={<ReviewIcon />} />
            <Tab  className="Tabs" label="Complaints" icon={<ComplaintIcon />} />
            <Tab  className="Tabs" label="Videos" icon={<VideoIcon />} />
        
          </Tabs>
        </AppBar>


        <br/>

        <Paper style={{padding:15,minHeight:210}}>
        {value === 0 && <TabContainer> A short description about school and its  facilites will be displayed here!</TabContainer>}
        {value === 1 && <TabContainer>Review system will appear here where registered parents or students can post their reviews
<br/>
<div class="clearfix">
<Review/>
 <input type="text" style={{width:'100%',height:50}}/>

 <Button style={{float:'right',margin:15}} variant="contained" color="primary" className={classes.button}>
        Submit
      </Button>
</div>
        </TabContainer>}
        {value === 2 && <TabContainer>Here Parents or Students can post their complaints about school or school teachers!.</TabContainer>}
        {value === 3 && <TabContainer>Videos about schools will be posted here!!</TabContainer>}
     

        </Paper>
      </div>
    );
  }
}

ScrollableTabsButtonForce.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ScrollableTabsButtonForce);
