import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Home as HomeIcon, PowerSettingsNew as LogoutIcon, ExitToApp as LoginIcon } from '@material-ui/icons';
import Notfound from './screens/404'
import Home from './screens/home'
import SchoolProfile from './screens/schoolProfile'
import { Router, Route, Link, Switch } from "react-router-dom";
import Login from './screens/login'
import createBrowserHistory from 'history/createBrowserHistory';
import fire from './config/firebase'
import Logoutbtn from './Components/logoutbtn'
import { store, persistor } from './Redux/store'
import { Provider } from 'react-redux'
import { PersistGate } from 'redux-persist/integration/react';
import AddSchool from './screens/AddSchool'
const History = createBrowserHistory();

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
});

class PersistentDrawerLeft extends React.Component {
  state = {
    open: false,
    loggedIn:null
  };

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };


  componentDidMount(){

    fire.auth().onAuthStateChanged((user)=> {
      if (user) {
        // User is signed in.
this.setState({loggedIn:true})
        // ...
      } else {
        // User is signed out.
        this.setState({loggedIn:false})

        // ...
      }
    });
  }


  render() {
    const { classes, theme } = this.props;
    const { open,loggedIn } = this.state;
const login = false
    return (
      <Provider store={store}>
      <PersistGate loading={null} persistor={persistor}>
      <Router history={History}>

        <div className={classes.root}>
          <CssBaseline />
          <AppBar
            position="fixed"
            className={classNames(classes.appBar, {
              [classes.appBarShift]: open,
            })}
          >
            <Toolbar disableGutters={!open}>
              <IconButton
                color="inherit"
                aria-label="Open drawer"
                onClick={this.handleDrawerOpen}
                className={classNames(classes.menuButton, open && classes.hide)}
              >
                <MenuIcon />
              </IconButton>
              <Typography variant="h6" color="inherit" noWrap>
                Schools Fetch            </Typography>
            </Toolbar>
          </AppBar>
          <Drawer
            className={classes.drawer}
            variant="persistent"
            anchor="left"
            open={open}
            classes={{
              paper: classes.drawerPaper,
            }}
          >
            <div className={classes.drawerHeader}>
              <IconButton onClick={this.handleDrawerClose}>
                {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
              </IconButton>
            </div>
            <Divider />
            <List>


{loggedIn ? (<span>
<Link  className="NoDecoration" to="/">
              <ListItem onClick={() => { this.setState({ open: false }) }} button >
                <ListItemIcon> <LoginIcon /></ListItemIcon>
                <ListItemText primary={"Home"} />
              </ListItem>
              </Link>




              <Link className="NoDecoration"   to="/AddSchool">
              <ListItem onClick={() => { this.setState({ open: false }) }} button >
                <ListItemIcon> <LoginIcon /></ListItemIcon>
                <ListItemText primary={"Add School"} />
              </ListItem>
              </Link>

<Logoutbtn loggedIn={loggedIn} />


</span>):(<span>

<Link  className="NoDecoration"  to="/Login">
              <ListItem onClick={() => { this.setState({ open: false }) }} button >
                <ListItemIcon> <LoginIcon /></ListItemIcon>
                <ListItemText primary={"Login"} />
              </ListItem>
              </Link>
</span>)}

            </List>
          </Drawer>
          <main
            className={classNames(classes.content, {
              [classes.contentShift]: open,
            })}
          >
            <div className={classes.drawerHeader} />










            <Switch>
              <Route exact path="/" component={Home} />

                            <Route exact path="/Login" component={Login} />

              <Route exact path="/School" component={Home} />

              <Route exact path="/School/:id" component={SchoolProfile} />
              <Route exact path="/AddSchool" component={AddSchool} />

              {/* <Route component={Testing} /> */}

              <Route component={Notfound} />


            </Switch>










          </main>
        </div>
      </Router>
      </PersistGate>
        </Provider>
    );
  }
}

PersistentDrawerLeft.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(PersistentDrawerLeft);