import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import classNames from 'classnames';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import ReactDOM from 'react-dom';
import Input from '@material-ui/core/Input';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FilledInput from '@material-ui/core/FilledInput';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import green from '@material-ui/core/colors/green';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import swal from 'sweetalert';
import firebase from '../../config/firebase'
import axios from 'axios';

const styles = theme => ({
    root: {
        width: '90%',
    },
    button: {
        marginTop: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    actionsContainer: {
        marginBottom: theme.spacing.unit * 2,
    },
    resetContainer: {
        padding: theme.spacing.unit * 3,
    },
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
    },
    dense: {
        marginTop: 19,
    },
    menu: {
        width: 200,
    },
});



class VerticalLinearStepper extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            activeStep: 0,
            categories: '',
            sName: '',
            contact: '',
            email: '',
            SLink: '',
            secondary: '',
            prePrimary: '',
            primary: '',
            address: '',
            library: false,
            ground: false,
            washrooms: false,
            canteen: false,
            lab : false,
            imag: '',
            urlImage: ''
        }

    }

    handleNext = () => {
        const { activeStep,sName,categories,email,contact,SLink,address,imag } = this.state
        !activeStep && sName && categories && email && contact && SLink ?
        this.setState({
            activeStep: activeStep + 1,
        }) : 
        activeStep == 1 && address && imag ? 
        this.setState({
            activeStep: activeStep + 1,
        }) : swal("Please Fill all the Fields")
    }

    handleBack = () => {
        this.setState(state => ({
            activeStep: state.activeStep - 1,
        }));
    };

    handleReset = () => {
        this.setState({
            activeStep: 0,
        });
    };

    handleChange = name => event => {
        this.setState({
            [name]: event.target.value,
        });
    };

    handleChange1 = name => event => {
    };

    submitData(){
        const { sName,contact,email,SLink,lab,ground,canteen,library,address,washrooms,imag } = this.state
        let filedata= new FormData();
        // const fees = {
        //     prePrimary,secondary,primary
        // }
        const facilities = {
            lab,ground,library,canteen,washrooms
        }
        filedata.append('file',imag);
        axios({
            url: '//reactsocial.mutualfilesharing.com/UploadedFiles/upload.php',
            method:'POST',
            headers:{'Content-Type':'application/x-www-form-urlencoded'},
            data: filedata
        }).then((res)=>{
            console.log(res)
            //var uploadedfilename = res.data.files[0];
              var downloadURL = '//reactsocial.mutualfilesharing.com/UploadedFiles/' +encodeURIComponent(imag.name.trim())
              const obj = {
                facilities,
                sName,
                contact,
                email,
                SLink,
                address,
                img: downloadURL
            }
            firebase.database().ref("schoolList").push(obj)
        })
    }
    imageData(e){
        this.setState({urlImage: URL.createObjectURL(e.target.files[0]),imag: e.target.files[0]})
    }

    getSteps() {
        return ['Basic School Details', 'Other School Details', 'Location'];
    }

    getStepContent(step) {
        switch (step) {
            case 0:
                return <div>
                    <TextField
                        id="standard-name"
                        label="School Name"
                        value={this.state.sName}
                        onChange={this.handleChange('sName')}
                        margin="normal"
                    />
                    <br />
                    {/* <TextField
                        select
                        label="Categories"
                        value={this.state.categories}
                        style={{ width: 200 }}
                        onChange={this.handleChange('categories')}
                    >
                        <MenuItem value={'Private'}>
                            Private
                        </MenuItem>
                        <MenuItem value={'Public'}>
                            Public
                        </MenuItem>
                        
                    </TextField> */}
                    <TextField
                        select
                        label="Categories"
                        value={this.state.categories}
                        style={{ width: 200 }}
                        onChange={this.handleChange('categories')}
                    >
                        <MenuItem value={'Boys'}>
                        Boys
                        </MenuItem>
                        <MenuItem value={'Girls'}>
                        Girls
                        </MenuItem>
                        
                    </TextField>

                    <br />
                    <TextField
                        id="standard-name"
                        label="Contact Number"
                        value={this.state.contact}
                        onChange={this.handleChange1('contact')}
                        margin="normal"
                    /><br />
                    <TextField
                        type='email'
                        id="standard-name"
                        label="Email"
                        value={this.state.email}
                        onChange={this.handleChange('email')}
                        margin="normal"
                    /><br />
                    <TextField
                        id="standard-name"
                        label="Website/Facebook Link"
                        value={this.state.SLink}
                        onChange={this.handleChange('SLink')}
                        margin="normal"
                    /><br />
                </div>;
            case 1:
                return <div>
                    <h3>Facilities Available:</h3>
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={this.state.library}
                                onChange={(e)=>this.setState({library : e.target.checked}) }
                                value="Library"
                            />
                        }
                        label="LIBRARY"
                    /><br />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={this.state.washrooms}
                                onChange={(e)=>this.setState({library : e.target.checked}) }
                                value="washrooms"
                            />
                        }
                        label="LIBRARY"
                    /><br />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={this.state.ground}
                                onChange={(e)=>this.setState({ground : e.target.checked}) }
                                value="Play Ground"
                            />
                        }
                        label="PLAY GROUND"
                    /><br />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={this.state.lab}
                                onChange={(e)=>this.setState({lab : e.target.checked}) }
                                value="IT Lab"
                            />
                        }
                        label="IT LAB"
                    /><br />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={this.state.canteen}
                                onChange={(e)=>this.setState({canteen : e.target.checked}) }
                                value="canteen"
                            />
                        }
                        label="CANTEEN"
                    /><br />
                    <TextField
                        onChange={(e)=> this.imageData(e)}
                        type="file"
                        id="standard-name"
                        label="School Name"
                        margin="normal"
                    />
                    <br />
                    {this.state.imag ? <img src={this.state.urlImage} width="300" height="300"/>
                     : null}
                    <br/>
                    {/* <h3>Fees Structure:</h3>
                    <TextField
                        id="standard-name"
                        label="Pre-Primary Class Fees"
                        value={this.state.prePrimary}
                        onChange={this.handleChange1('prePrimary')}
                        margin="normal"
                    /><br />
                    <TextField
                        id="standard-name"
                        label="Primary Class Fees"
                        value={this.state.primary}
                        onChange={this.handleChange1('primary')}
                        margin="normal"
                    /><br />
                    <TextField
                        id="standard-name"
                        label="secondary Class Fees"
                        value={this.state.secondary}
                        onChange={this.handleChange1('secondary')}
                        margin="normal"
                    /><br /> */}
                    <TextField
                        id="standard-name"
                        label="School Address"
                        value={this.state.address}
                        onChange={this.handleChange('address')}
                        margin="normal"
                    /><br />
                </div>;
            case 2:
                return `Try out different ad text to see what brings in the most customers,
                and learn how to enhance your ads using features like ad extensions.
                If you run into any problems with your ads, find out how to tell if
                they're running and how to resolve approval issues.`;
            default:
                return 'Unknown step';
        }
    }





    render() {
        const { classes } = this.props;
        const steps = this.getSteps();
        const { activeStep } = this.state;
        // console.log('this****', this.state)

        return (
            <div className={classes.root}>
                <Stepper activeStep={activeStep} orientation="vertical">
                    {steps.map((label, index) => {
                        return (
                            <Step key={label}>
                                <StepLabel>{label}</StepLabel>
                                <StepContent>
                                    <Typography>{this.getStepContent(index)}</Typography>
                                    <div className={classes.actionsContainer}>
                                        <div>
                                            <Button
                                                disabled={activeStep === 0}
                                                onClick={this.handleBack}
                                                className={classes.button}
                                            >
                                                Back
                      </Button>
                                            {activeStep === steps.length - 1 ? <Button
                                                variant="contained"
                                                color="primary"
                                                onClick={()=> this.submitData()}
                                                className={classes.button}
                                            >Finish
                                            </Button> : <Button
                                                variant="contained"
                                                color="primary"
                                                onClick={this.handleNext}
                                                className={classes.button}
                                            >Next
                                            </Button>}
                                        </div>
                                    </div>
                                </StepContent>
                            </Step>
                        );
                    })}
                </Stepper>
                {activeStep === steps.length && (
                    <Paper square elevation={0} className={classes.resetContainer}>
                        <Typography>All steps completed - you&apos;re finished</Typography>
                        <Button onClick={this.handleReset} className={classes.button}>
                            Reset
            </Button>
                    </Paper>
                )}
            </div>
        );
    }
}

VerticalLinearStepper.propTypes = {
    classes: PropTypes.object,
};

export default withStyles(styles)(VerticalLinearStepper);