import React from 'react'
import Card from '../../Components/card'


import fire from '../../config/firebase'
import {data,loggedIn} from '../../Redux/action/autjAction'
import { connect } from 'react-redux'
import swal from 'sweetalert';
import {
  Link
} from 'react-router-dom';

 class Home extends React.Component {

constructor(props){

  super(props)
}



  componentDidMount(){
    let arr = [];
  fire.database().ref('schoolList').on('child_added', e => {
      this.props.data(null)

      arr.push({key:e.key,...e.val()})
      this.props.data(arr)
  })
}



  render() {
console.log(this.props.d)
const {d,loggedIn} = this.props

console.log(this.props)
    return (
      <div> 


{d && d.map((data,i)=>{


return       <Link to={`/School/${data.key}`}>  <center style={{display:'inline-block'}}> <Card  Slink={data.Slink} contact={data.contact} email={data.email} facilities={data.facilities} fees={data.fees} sName={data.sName} pic={data.img} /> </center> </Link>


})}
        

       
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
      d: state.authReducers.data
  }
} 

const mapDispatchToProps = (dispatch) => {
  return {
      data: (d) => dispatch(data(d)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home)